    const res: any = await download(item);

    try {
        let blob = new Blob([res.data])
        const url = window.URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.href = url;
        link.download = item;
        link.click();
        window.URL.revokeObjectURL(url);
    } catch (e) {
        ElMessage.error("下载失败");
    } finally {
        findFiles();
        loading.show = false;
    }
}


